*{
	margin: 0;
	padding: 0;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}

.zx-mask {
	font-size: 0.3rem;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 9999;
	text-align: center;
  background: red;
}

/***************************对话框******************************/
.dialog-box {
  background: rgba(0,0,0,0.4);
}
.dialog-box > div {
  overflow: hidden;
  position: absolute;
  left: 13.5%;
  top: 50%;
  width: 73%;
	background: #FFFFFF;
	border-radius: 0.2rem;
	text-align: center;
  vertical-align: middle;
	-ms-transform: translate(0, -50%); 	/* IE 9 */
	-moz-transform:translate(0, -50%); 	/* Firefox */
	-o-transform:translate(0, -50%); 	/* Opera */
	-webkit-transform:translate(0, -50%); /* Safari 和 Chrome */
	transform: translate(0, -50%);
}
.dialog-box > div > h1 {
	font-size: 0.3rem;
	font-style: normal;
	padding: 0.3rem 0.2rem 0.2rem;
}
.dialog-box > div > p {
	font-size: 0.28rem;
	display: block;
	/*min-height: 0.8rem;*/
  line-height: 0.4rem;
	padding: 0 0.4rem 0.3rem;
}
.dialog-box > div > a {
	font-size: 0.32rem;
	display: block;
	line-height: 0.8rem;
	color: #007AFF;
	text-decoration: none;
	border-top: 1px solid #DBDCDC;
}
.dialog-box i {
	font-style: normal;
	float: left;
	width: 50%;
}
.dialog-box i:first-child {
	margin-right: -1px;
	border-right: 1px solid #DBDCDC;
}


/****************************toast**************************/
.toast-box {
  font-size: 0.28rem !important;
  position: fixed;
  left: 0;
  top: 50%;
  width: 100%;
  text-align: center;
	background: rgba(0,0,0,0);
	animation: toast-fadein 3s forwards;
	-webkit-animation: toast-fadein 3s forwards;
  z-index: 9999;
}
.toast-box > p {
	overflow: hidden;
	position: relative;
	display: inline-block;
  line-height: 0.38rem;
	max-width: 60%;
	padding: 0.15rem;
	color: #ffffff;
	background: #535353;
	border-radius: 0.15rem;
	/*-ms-transform: translate(0, -50%); 	/* IE 9 */
/*	-moz-transform:translate(0, -50%); */	/* Firefox */
/*	-o-transform:translate(0, -50%); */	/* Opera */
/*	-webkit-transform:translate(0, -50%);*/ /* Safari 和 Chrome */
	/*transform: translate(0, -50%);*/
}
@keyframes toast-fadein {
	0% { opacity: 0.2; }
	40% { opacity: 1; }
	60% { opacity: 1; }
	100% { opacity: 0;}
}
@-webkit-keyframes toast-fadein {
	0% { opacity: 0.2; }
	40% { opacity: 1; }
	60% { opacity: 1; }
	100% { opacity: 0;}
}


/***********************菊花loading效果**************************/
.loadindBox {
  background: rgba(0,0,0,0);
  /*background: white;*/
}
.loadindBox > div {
  overflow: hidden;
  font-size: 14px;
  position: absolute;
  top: 50%;
  left: 50%;
  display: inline-block;
	min-width: 60px;
	max-width: 140px;
	padding: 8px 15px 10px;
	color: #ffffff;
	background: #535353;
	border-radius: 8px;
	text-align: center;

	-ms-transform: translate(-50%, -50%); 	/* IE 9 */
	-moz-transform:translate(-50%, -50%); 	/* Firefox */
	-o-transform:translate(-50%, -50%); 	/* Opera */
	-webkit-transform:translate(-50%, -50%); /* Safari 和 Chrome */
	transform: translate(-50%, -50%);
}
.loadindBox > div > p {
  font-size: 14px;
  min-width: 80px;
}
.loadindBox > div > div{
	position: relative;
	width: 40px;
	height: 40px;
	margin: 0 auto;
}
.loadindBox span {
	display: block;
	position: absolute;
	left: 18px;
	top: 16px;
	width:2px;
	height:6px;
	opacity: 0;
	background: #ffffff;
	-webkit-border-radius: 2px;
  -webkit-animation: loading 2s linear infinite;
  animation: loading 2s linear infinite;
}
@-webkit-keyframes loading {
	/*from {opacity: 1;width:1px;height:1px;}*/
	/*to {opacity: 0.25;width:8px;height:8px;}*/
	from {opacity: 1;}
	to {opacity: 0.3;}
}
@keyframes loading {
  from {opacity: 1;}
  to {opacity: 0.3;}
}
.loadindBox span:nth-child(1) {-webkit-transform:rotate(0deg) translate(0, -9px); -webkit-animation-delay: -0s;}
.loadindBox span:nth-child(2) {-webkit-transform:rotate(30deg) translate(0, -9px); -webkit-animation-delay: -1.8334s;}
.loadindBox span:nth-child(3) {-webkit-transform:rotate(60deg) translate(0, -9px); -webkit-animation-delay: -1.6668s;}
.loadindBox span:nth-child(4) {-webkit-transform:rotate(90deg) translate(0, -9px); -webkit-animation-delay: -1.5002s;}
.loadindBox span:nth-child(5) {-webkit-transform:rotate(120deg) translate(0, -9px); -webkit-animation-delay: -1.3336s;}
.loadindBox span:nth-child(6) {-webkit-transform:rotate(150deg) translate(0, -9px); -webkit-animation-delay: -1.167s;}
.loadindBox span:nth-child(7) {-webkit-transform:rotate(180deg) translate(0, -9px); -webkit-animation-delay: -1.0004s;}
.loadindBox span:nth-child(8) {-webkit-transform:rotate(210deg) translate(0, -9px); -webkit-animation-delay: -0.8338s;}
.loadindBox span:nth-child(9) {-webkit-transform:rotate(240deg) translate(0, -9px); -webkit-animation-delay: -0.6672s;}
.loadindBox span:nth-child(10) {-webkit-transform:rotate(270deg) translate(0, -9px); -webkit-animation-delay: -0.5006s;}
.loadindBox span:nth-child(11) {-webkit-transform:rotate(300deg) translate(0, -9px); -webkit-animation-delay: -0.334s;}
.loadindBox span:nth-child(12) {-webkit-transform:rotate(330deg) translate(0, -9px); -webkit-animation-delay: -0.1674s;}

/************************loadingBounce动画**************************/
.loadingBounce {
  /*background-color: #efeef3;*/
  background-color: white;
}
.loadingBounce > p {
  text-align: center;
  margin-top: .4rem;
}

.loadingBounce > div {
  width: .6rem;
  height: .6rem;
  position: relative;
  margin: 0 auto;
  margin-top: 5rem;
}

.loadingBounce > div > div {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #00AAEF;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: loadBounce 2.0s infinite ease-in-out;
  animation: loadBounce 2.0s infinite ease-in-out;
}

.loadingBounce > div > div:last-child {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}

@-webkit-keyframes loadBounce {
  0%, 100% {
    -webkit-transform: scale(0.0);
  }
  50% {
    -webkit-transform: scale(1.0);
  }
}

@keyframes loadBounce {
  0%, 100% {
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
  }
  50% {
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
}

/*清除*/
.clearloadBounce {
  -webkit-animation: clearload 1s forwards;
  animation: clearload 1s forwards;
}
@keyframes clearload {
  0% { opacity: 1;}
  100% {opacity: 0;}
}
@-webkit-keyframes clearload {
  0% {opacity: 1;}
  100% {opacity: 0;}
}

/*****************************查看图片**************************/
#lookImage {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: black;
	z-index: 9999;
}
#lookImage > div {
	position:relative;
	display: -webkit-box !important;
	display: box !important;
	-webkit-box-align:center;
	box-align:center;
	width: 100%;
	height: 100%;
}
#lookImage > div > div {
	position:relative;
	-webkit-box-flex: 1;
	box-flex: 1;
}
#lookImage > div > p {
	position:relative;
	-webkit-box-flex: 1;
	box-flex: 1;
	margin: 0 auto;
	text-align: center;
	vertical-align: middle;
}
#lookImage > div > p > img {
	vertical-align: middle;
}

/*图片loading效果*/
.loadingImages {
	font-size: 13px;
	width: 30px;
	height: 30px;
	margin: 0 auto;
	/*background: #999;*/
	text-align: center;
}
.loadingImages > div {
	position: relative;
	overflow: hidden;
	display: inline-block;
}
.loadingImages > div > span, .loadingImages > div > span:after {
	display: block;
	width: 30px;
	height: 30px;
	border-radius: 50%;
}
.loadingImages > div > span {
	display: block;
	position: relative;
	margin: 0 auto;
	border-top: 3px solid rgba(255, 255, 255, 0.3);
	border-right: 3px solid rgba(255, 255, 255, 0.3);
	border-bottom: 3px solid rgba(255, 255, 255, 0.3);
	border-left: 3px solid #ffffff;
	-webkit-transform: translateZ(0);
	-moz-transform: translateZ(0);
	-ms-transform: translateZ(0);
	-o-transform: translateZ(0);
	transform: translateZ(0);
	-webkit-animation: load_img 1s infinite linear;
	animation: load_img 1s infinite linear;
}

@-webkit-keyframes load_img {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@keyframes load_img {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}




